<template>
  <v-speed-dial
    ref="speedDial"
    v-model="button_fab"
  >
    <v-btn
      class="confirm-delete-activator"
      icon
      slot="activator"
      fab
      v-model="button_fab"
      @click.stop.prevent="button_fab = !button_fab"
      @keydown.up="activatorButtonHandleKeys"
    >
      <v-icon color="red">delete</v-icon>
      <v-icon color="red">close</v-icon>
    </v-btn>
    <v-btn
      fab
      small
      ref="confirm_btn"
      @click.stop.prevent="destroy"
    >
      <v-icon color="green">done</v-icon>
    </v-btn>
  </v-speed-dial>
</template>

<script>
export default {
  name: 'ConfirmDestroy',
  mounted() {
    document.addEventListener('click', this.handleClickOutside);
  },
  methods: {
    destroy() {
      this.$emit('delete');
      this.button_fab = false;
    },
    activatorButtonHandleKeys() {
      this.$refs.confirm_btn.$el.focus();
    },
    isSameElement(target) {
      return target !== this.$el && !this.$el.contains(target);
    },
    handleClickOutside(event) {
      if (this.isSameElement(event.target)) {
        this.button_fab = false;
      }
    },
  },
  data: () => ({
    button_fab: false,
  }),
};
</script>
